.ingred__backdrop {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: $backdrop;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__ingred {
  width: 100%;
  max-width: 333px;
  background: $basic-white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @include tablet {
    max-width: 715px;
  }
  @include desktop {
    max-width: 715px;
  }
}

.modal__ingred-close {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 32px;
  height: 32px;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.ingred__backdrop.is-hidden .modal {
  transform: translate(-50%, -50%) scale(0);
}

.ingred__border {
  display: none;
  height: 30px;
  padding-bottom: 40px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: $accent-color;
  @include tablet {
    display: block;
  }
  @include desktop {
    display: block;
  }
}

.ingred__name {
  font-family: 'Roboto';
  font-weight: 900;
  font-size: 32px;
  line-height: 38px;
  color: $black;

  margin-top: 40px;
  margin-left: 20px;

  @include tablet {
    margin-left: 28px;
  }
  @include desktop {
    margin-left: 28px;
  }
}

.ingred__type {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: $black;

  margin-top: 20px;
  margin-left: 20px;

  @include tablet {
    margin-top: 28px;
    margin-left: 28px;
  }
  @include desktop {
    margin-top: 28px;
    margin-left: 28px;
  }
}

.ingred__info {
  font-size: 16px;
  line-height: 1.75;
  color: $grey;

  margin: 20px;
  margin-bottom: 28px;

  @include tablet {
    margin: 28px;
  }
  @include desktop {
    margin: 28px;
  }
}

.ingred__info-bold {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.75;
  color: $black-modal;
}

.ingred__list {
  margin-left: 16px;

  @include tablet {
    margin-left: 28px;
  }
  @include desktop {
    margin-left: 28px;
  }
}

.ingred__item {
  font-size: 16px;
  line-height: 1.62;
  color: $grey;

  &::before {
    content: 'âœ¶ ';
    background-repeat: no-repeat;
    color: $black-modal;
  }
}

.favorite__btn {
  display: block;
  margin-left: 20px;
  margin-right: auto;
  margin-bottom: 40px;
  margin-top: 32px;
  width: 185px;
  height: 48px;
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.02em;
  color: $basic-white-text;
  background-color: $accent-color;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  @include tablet {
    margin-left: auto;
    margin-right: 28px;
    margin-bottom: 28px;
    margin-top: 40px;
  }
  @include desktop {
    margin-left: auto;
    margin-right: 28px;
    margin-bottom: 28px;
    margin-top: 40px;
  }

  &:hover,
  &:focus {
    background-color: $accent-color-hover;
  }
}
